import React, {Fragment} from 'react';
import { withRouter } from 'react-router-dom';
import firebaseApp from '../config/firebase' 
import Ruta from '../components/Ruta.js' 
import logo from '../img/chofer.png'
import '../css/homeAdmi2.css'
import { BrowserRouter,Link,Route} from 'react-router-dom'
import { Navbar, NavbarBrand, NavbarNav, NavbarToggler, Collapse, NavItem, NavLink} from 'mdbreact';

class Homeadmi2 extends React.Component  {

    constructor(props){
        super(props);
  
       
  
        this.state = {
          email: '',
          password: '',
          collapse: false
        };

        this.onClick = this.onClick.bind(this);
}


onClick(){
    this.setState({
        collapse: !this.state.collapse,
      });
  }



  logout(){
    firebaseApp.auth().signOut()
    .then(function() {
        console.log('Saliendo...');
      })
      .catch(function(error) {
        console.log(error);
      });
  }



render(){

    return (
        
        <BrowserRouter>
        <Fragment>
        
          <Navbar id="nav" dark expand="md" scrolling>
              <NavbarBrand href="/">
              <img width="60px"  src={logo} alt="logo"/>  <span id="titule2">Here Bus</span>
              
              </NavbarBrand>
              <NavbarToggler onClick={this.onClick } />
              <Collapse isOpen = { this.state.collapse } navbar>
                  <NavbarNav left id="tituleNav">
                    <NavItem active>
                       
                    </NavItem>
                    <NavItem>
                            <NavLink to="#">
                            <Link id="titule3" to='/Ruta'>
                            Ruta
                            </Link>
                             </NavLink>
                        </NavItem>

                        <NavItem>
                            <NavLink to="#">
                            <Link id="titule3" to='/Calendario'>
                            Calendario
                            </Link>
                             </NavLink>
                        </NavItem>

                        <NavItem>
                            <NavLink to="#">
                            <Link id="titule3" to='/Cancelar asistencia'>
                            Cancelar asistencia 
                            </Link>
                             </NavLink>
                        </NavItem>


                  </NavbarNav>
                  <NavbarNav right >
                    
                    <NavItem>
                    <NavLink onClick={this.logout} to="#" id="titule3" >Cerrar Sesión </NavLink>
                    </NavItem>
                  </NavbarNav>
              </Collapse>
          </Navbar>
     

    
     <Route path='/Ruta' component={Ruta} />
    
    



​
​   
       
</Fragment>
</BrowserRouter>
  );

}
    };


    export default  withRouter(Homeadmi2);