import React, { Component } from 'react';


class Ruta extends Component {
  constructor (){
    super();
    this.state={
    flags: null
    }
  }

      componentWillMount() {
    const flags = `https://image.maps.api.here.com/mia/1.6/mapview?poi=19.410936%2C%20-99.166950%2C%2019.411488%2C-99.1683357%2C%2019.4121042%2C-99.1704192%2C%2019.409264%2C%20-99.167467%2C%20&poitxs=16&poitxc=black&poifc=yellow&app_id=uXKmufo4aXIXBS5nw4HA&app_code=kQg_zysltomrOggVDhzuvw`
    this.setState({flags})
    }

  
    7

      render() {
    if(this.state){
      return(
        <div className="container">
          <img className="responsive" src={this.state.flags} alt='mapa'/>
        </div>
      )
    }else{
    return (
      <div className="App">
        <p>no hay estado</p>
      </div>
    );
  }
  }
}

export default Ruta;